import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, onlyNumber, WindowComponent } from 'summer';
import { CLASS_NAME, InfoComplementarProcessoJudicial } from '../../../../models/dtos/mfp/infoComplementarProcessoJudicial';
import CepLocalidadeIS from '../../../components/ger/cepLocalidadeIS/CepLocalidadeIS';
import CepUFIS from '../../../components/ger/cepUFIS/CepUFIS';

interface InfoComplementarProcessoJudicialEditViewProps extends EditViewProps<InfoComplementarProcessoJudicial> {}

const InfoComplementarProcessoJudicialEditView: FC<InfoComplementarProcessoJudicialEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<InfoComplementarProcessoJudicial>({ defaultValues: selectedItem, mode: 'onBlur' });

  useEffect(() => {
    if (selectedItem.id) {
      setTimeout(() => {
        formProps.setValue('ufVara', { ...selectedItem.ufVara, label: selectedItem.ufVara.nome });
        formProps.setValue('municipio', { ...selectedItem.municipio, label: selectedItem.municipio.nome });
      }, 500);
    }
  });

  return (
    <WindowComponent title="Manutenção de Informação Complementar de Processo Judicial">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <CepUFIS id="ufVara" formProps={{ ...formProps, validation: { required: true } }} />
          </Col>
          <Col>
            <CepLocalidadeIS id="municipio" formProps={{ ...formProps, validation: { required: true } }} />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, ...onlyNumber(0, 9999), maxLength: 4 } }}
              id="numeroIdentificacaoVara"
              isPrependLabel={true}
              label="Nº Identificação Vara"
              tooltipProps={{ message: 'Número de Identificação da Vara.' }}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default InfoComplementarProcessoJudicialEditView;
